<template>
  <div class="ordertypepage">
    <div class="orderspage">
      <el-form
        class="titleform"
        :inline="true"
        label-width="100px"
        style="line-height: 36px; padding-top: 20px"
      >
        <el-form-item label="订单编号:" label-width="80px">
          <el-input
            class="searchipt"
            placeholder="请输入订单编号"
            v-model="page.orderNo"
          ></el-input>
        </el-form-item>
      </el-form>
      <div class="block">
        <span class="demonstration">选择日期：</span>
        <el-date-picker
          value-format="yyyy-MM-dd"
          :unlink-panels="true"
          v-model="value1"
          type="daterange"
          range-separator="~"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
        >
        </el-date-picker>
        <el-button
          type="primary"
          icon="el-icon-search"
          class="addbtn"
          @click="search"
          >查询</el-button
        >
      </div>
    </div>
    <div class="ordersbody">
      <template>
        <el-table
          :data="tableData"
          style="width: 100%;font-size:10px"
          :header-cell-style="{ background: '#f3f6fb' }"
          :row-style="{height:'50px'}"
          class="tablebox"
        >
          <el-table-column type="index" label="序号" width="80">
            <template slot-scope="scope">
              {{ scope.$index + (page.pageIndex - 1) * 10 + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            prop="orderNo"
            label="订单编号"
            width="180"
            show-overflow-tooltip=""
          >
          </el-table-column>
          <el-table-column prop="skuName" label="商品名称" width="180">
          </el-table-column>
          <el-table-column prop="price" label="订单金额(元)"> </el-table-column>
          <el-table-column prop="innerCode" label="设备编号"> </el-table-column>
          <el-table-column
            prop="status"
            label="订单状态"
            :formatter="formatStatus"
          >
          </el-table-column>
          <el-table-column prop="updateTime" label="订单日期">
          </el-table-column>
          <el-table-column prop="address" label="操作" width="100">
            <template slot-scope="scope">
              <el-button
                type="text"
                style="color: #4368e6"
                @click="detail(scope.row.id)"
                >查看详情</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="page.pageIndex"
      :page-size="page.pageSize"
      background
      layout="total,prev, pager, next"
      :total="total"
    >
    </el-pagination>
    <el-dialog title="订单详情" :visible.sync="dialogVisible" width="45%">
      <div class="orderstatus">
        <img
          :src="beforeimg"
          alt=""
          class="icon"
        />
        <span class="status">{{status}}</span>
        <img
          :src="afterimg"
          alt=""
        />
      </div>
      <el-form>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="订单编号:">
              {{ chooseList.orderNo }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="商品名称:">
              {{ chooseList.skuName }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="设备编号:">
              {{ chooseList.innerCode }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="订单金额:">
              {{ chooseList.price }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="完成时间:">
              {{ chooseList.updateTime }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="创建时间:">
              {{ chooseList.createTime }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="支付方式:">
              {{ chooseList.payType === 1 ? "支付宝支付" : "微信支付" }}
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="设备地址:">
              {{ chooseList.addr }}
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer"> </span>
    </el-dialog>
  </div>
</template>

<script>
import { getOrderList } from '@/api/ordermanagement'
export default {
  data () {
    return {
      beforeimg: '',
      afterimg: '',
      status: '',
      dialogVisible: false,
      // 选择的时间的数据
      value1: '',
      // 渲染的数组
      tableData: [],
      // 请求参数
      page: {
        pageIndex: 1,
        pageSize: 10,
        orderNo: ''
      },
      total: 0,
      chooseList: []
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    // 获取订单列表
    async getOrderList () {
      const res = await getOrderList(this.page)
      // console.log(res)
      res.currentPageRecords.forEach((item) => {
        item.price = (item.price / 100).toFixed(2)
        item.updateTime = item.updateTime.replace('T', ' ')
        item.createTime = item.updateTime.replace('T', ' ')
      })
      // console.log(res)
      this.tableData = res.currentPageRecords
      this.total = parseInt(res.totalCount)
    },
    // 翻页重新渲染
    handleCurrentChange (val) {
      this.page.pageIndex = val
      this.getOrderList()
    },
    // 判断订单状态
    formatStatus (row, column, cellValue, index) {
      console.log(row)
      if (row.payStatus === 1) return '支付完成'
      if (row.status === 4) return '已失效'
      if (row.payStatus === 0) return '未支付'
    },
    // 搜索订单
    async search () {
      const [startTime, endTime] = this.value1
      this.page.startDate = startTime
      this.page.endDate = endTime
      await getOrderList(this.page)
      // console.log(res)
      this.getOrderList()
    },
    detail (id) {
      this.dialogVisible = true
      this.chooseList = this.tableData.filter((item) => item.id === id)
      const [listDetail] = this.chooseList
      this.chooseList = listDetail
      console.log(this.chooseList)
      if (this.chooseList.status === 2) {
        this.status = '出货成功'
        this.beforeimg = 'data:image/png;base64,'
        this.afterimg = 'https://likede2-admin.itheima.net/img/pic_2.3b5af41c.png'
      }
      if (this.chooseList.payStatus === 0) {
        this.status = '未支付'
        this.beforeimg = 'data:image/png;base64,'
        this.afterimg = 'https://likede2-admin.itheima.net/img/pic_0.979e683d.png'
      }
      if (this.chooseList.status === 4) {
        this.status = '已失效'
        this.beforeimg = 'data:image/png;base64,'
        this.afterimg = 'https://likede2-admin.itheima.net/img/pic_4.e8208e34.png'
      }
      // console.log(this.chooseList.price)
    }
  }
  // 测试
  // updated () {
  //   console.log(this.value1)
  //   const [startTime, endTime] = this.value1
  //   console.log(startTime)
  //   console.log(endTime)
  // }
}
</script>

